  background: #0B0C0E;
}
#header .navbar-brand {
  padding: 7px 0px;
  margin-right: auto;
}
#header .navbar-brand img {
  height: 46px;
}
#header .navbar-toggler {
  padding: 0;
  border: none;
  border-radius: 0px;
  transition: none;
}
#header .navbar-toggler .navbar-toggler-icon {
  width: 21px;
  height: 24px;
  background: transparent url(../img/icons/menu.svg) center center no-repeat;
  background-size: cover;
}
#header .navbar-nav .nav-item .nav-link {
  padding: 8px 0px;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.03em;
  color: #FFFFFF;
}
#header .navbar-nav.main {
  grid-column-gap: 53px;
  column-gap: 53px;
}
#header .navbar-nav.side {
  grid-column-gap: 25px;
  column-gap: 25px;
}

